html {
  font-size: 62.5%;
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem; }

p {
  font-size: 2rem;
  color: #000000; }

.contenedor {
  max-width: 100rem;
  width: 95%;
  margin: 0 auto; }

a {
  text-decoration: none; }

img {
  max-width: 100%; }

h1, h2, h3 {
  margin: 0; }

h1 {
  font-size: 4rem;
  font-weight: 900; }

h2 {
  font-size: 3.6rem; }

h3 {
  font-size: 2.6rem; }

.header {
  background-color: #4cb8b3; }
  .header .contenido-header {
    padding: 2.5rem 0;
    display: flex;
    justify-content: space-between;
    align-items: center; }
    @media (max-width: 768px) {
      .header .contenido-header {
        display: block;
        text-align: center; } }
  .header h1 {
    color: #ffffff; }
    @media (max-width: 768px) {
      .header h1 {
        margin-bottom: 2rem; } }
  .header.fijo {
    position: fixed;
    width: 100%; }

.navegacion-principal a {
  color: #ffffff;
  font-size: 1.6rem;
  margin-right: 2rem; }
  .navegacion-principal a:hover {
    color: #fef46d; }
  .navegacion-principal a:last-of-type {
    margin: 0; }

.video {
  position: relative;
  max-height: 70rem;
  overflow: hidden; }
  .video .overlay {
    position: absolute;
    width: 100%;
    height: 100%;
    /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#891c1e+0,9e37f2+64,ffffff+98&0.8+0,0.5+98 */
    background: -moz-linear-gradient(-45deg, rgba(137, 28, 30, 0.8) 0%, rgba(158, 55, 242, 0.6) 64%, rgba(255, 255, 255, 0.5) 98%);
    /* FF3.6-15 */
    background: -webkit-linear-gradient(-45deg, rgba(137, 28, 30, 0.8) 0%, rgba(158, 55, 242, 0.6) 64%, rgba(255, 255, 255, 0.5) 98%);
    /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(135deg, rgba(137, 28, 30, 0.8) 0%, rgba(158, 55, 242, 0.6) 64%, rgba(255, 255, 255, 0.5) 98%);
    /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#cc891c1e', endColorstr='#80ffffff',GradientType=1 );
    /* IE6-9 fallback on horizontal gradient */ }
  .video .overlay-contenido {
    display: flex;
    align-items: center;
    height: 100%;
    position: relative;
    z-index: 1; }
    .video .overlay-contenido h2 {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      color: #ffffff;
      font-size: 7rem;
      margin: 0 auto; }
      @media (max-width: 560px) {
        .video .overlay-contenido h2 {
          font-size: 3rem; } }
      .video .overlay-contenido h2 span {
        background-color: #f53756;
        font-size: 2rem;
        padding: 1rem 2rem; }
        @media (max-width: 560px) {
          .video .overlay-contenido h2 span {
            font-size: 1rem; } }
  .video video {
    width: 100%; }

.informacion {
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 5rem;
  margin-top: 5rem;
  padding-bottom: 5rem; }
  @media (max-width: 560px) {
    .informacion {
      display: block; } }
  .informacion .fecha {
    color: #f53756;
    font-weight: 700; }
  .informacion .info {
    text-align: justify;
    line-height: 1.5; }

h4, h3, .dia {
  text-align: center;
  color: #ffffff; }

.line-up {
  background-color: #f53756;
  padding: 5rem 0; }
  .line-up .escenarios {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 5rem; }
    @media (max-width: 560px) {
      .line-up .escenarios {
        display: block; } }
  .line-up .escenario {
    border: 1px solid; }

.nombre_escenario {
  color: #000000;
  font-size: 2rem;
  font-weight: 300; }
  .nombre_escenario:after {
    content: '';
    width: 20rem;
    height: 10rem;
    display: block;
    margin: 2rem auto 0 auto;
    background-size: 15rem;
    background-repeat: no-repeat;
    background-position: center center; }

.calendario {
  padding: 0;
  list-style: none; }
  .calendario li {
    padding: 2.5rem;
    font-weight: 700; }
  .calendario span {
    font-weight: 400; }

.bg-amarillo .calendario li:nth-child(even) {
  background-color: #fef03a; }

.bg-verde .calendario li:nth-child(even) {
  background-color: #3b9691; }

.edm .nombre_escenario:after {
  background-image: url("../img/icono_edm.png"); }

.rock .nombre_escenario:after {
  background-image: url("../img/icono_rock.png"); }

.bg-amarillo {
  background-color: #fef46d; }

.bg-verde {
  background-color: #4cb8b3; }

.titulo_galeria {
  color: #000000;
  padding: 2rem; }

.galeria-imagenes {
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  gap: 1rem; }
  @media (max-width: 768px) {
    .galeria-imagenes {
      grid-template-columns: repeat(4, 1fr); } }
  @media (max-width: 560px) {
    .galeria-imagenes {
      grid-template-columns: repeat(2, 1fr); } }

.overlay {
  position: fixed;
  height: 100%;
  width: 100%;
  top: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: grid;
  place-items: center; }
  .overlay img {
    max-width: 50%; }

.btn-cerrar {
  font-size: 2rem;
  padding: 1rem;
  font-weight: 700;
  display: grid;
  place-items: center;
  background-color: white;
  border-radius: 50%;
  cursor: pointer; }

.scroll {
  overflow: hidden; }

.titulo_boletos {
  text-align: center;
  color: #ffffff;
  padding: 5rem;
  font-weight: 900; }

.boletos {
  background-color: #4cb8b3;
  padding-bottom: 5rem; }

.contenedor-pases {
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 5rem; }

.titulo_pase {
  text-align: center;
  color: #ffffff;
  font-weight: 700; }

.pase {
  width: 100%;
  border-spacing: 0; }
  .pase thead {
    background-color: #f53756; }
  .pase th {
    padding: 1.25rem;
    color: #ffffff;
    font-weight: 400;
    font-size: 2rem; }

tbody {
  background-color: #ffffff; }
  tbody td {
    padding: 1.25rem;
    border-bottom: 1px solid #e1e1e1; }

tfoot td {
  padding: 1.25rem;
  background-color: #752f97;
  color: #ffffff;
  font-weight: 700;
  font-size: 3rem;
  text-align: center; }

.footer {
  background-color: #f53756;
  text-align: center;
  padding: 1rem; }
  .footer p {
    color: #ffffff;
    margin: 0; }
